import React, { useState, useEffect, useRef } from "react";
import NavBar from "./NavBar";
import SideDrawer from "./SideDrawer";
import VideoLanding from "./VideoLanding";
import Backdrop from "../Backdrop/Backdrop";
import { ReactComponent as BruutLogo } from "../../images/bruut-logo-full-width.svg";
import { ReactComponent as ScrollArrow } from "../../images/scroll-down-arrow.svg";
import { scrollToSection } from "../../utils/gsap-animations/scrollToSection";
import "./Header.scss";

function Header() {
  const [sideDrawer, setSideDrawer] = useState(false);
  const [scrolledToTop, setScrolledToTop] = useState(false);
  const [initialViewportWidth, setInitialViewportWidth] = useState(null);
  const [initialViewportHeight, setInitialViewportHeight] = useState(null);

  const headerRef = useRef(null);
  const maxMobileWidth = 480;

  const setHeaderHeight = () => {
    return {
      height:
        window.innerWidth > maxMobileWidth || window.innerWidth < initialViewportWidth
          ? "100vh"
          : `${initialViewportHeight}px`
    };
  };

  useEffect(() => {
    setInitialViewportHeight(window.innerHeight);
    setInitialViewportWidth(window.innerWidth);
  }, []);

  useEffect(() => {
    const watchScrollPosition = () => {
      window.scrollY > 0 ? setScrolledToTop(true) : setScrolledToTop(false);
    };

    window.addEventListener("scroll", watchScrollPosition);

    return () => {
      window.removeEventListener("scroll", watchScrollPosition);
    };
  }, [scrolledToTop, setScrolledToTop]);

  return (
    <header ref={headerRef} className="header" style={setHeaderHeight()}>
      <NavBar setSideDrawer={setSideDrawer} sideDrawer={sideDrawer} />
      <SideDrawer sideDrawer={sideDrawer} setSideDrawer={setSideDrawer} />
      {sideDrawer && <Backdrop setSideDrawer={setSideDrawer} />}
      <VideoLanding />
      <BruutLogo className={scrolledToTop ? "logo-large invisible" : "logo-large"} />
      <div
        className="scroll-anchor"
        onClick={() => {
          scrollToSection(300, 0, 1, true);
        }}
      >
        <ScrollArrow />
      </div>
    </header>
  );
}

export default Header;