import gsap from 'gsap';
import { ScrollToPlugin } from 'gsap/ScrollToPlugin';
gsap.registerPlugin(ScrollToPlugin);


export function scrollToSection (section, margin, duration = 1, autoKill = true) {
  gsap.to(window, {
    duration,
    scrollTo: {
      y: section,
      autoKill,
      offsetY: margin,
      ease: 'power4.out',
    }
  });
}